.ClubSection{
    width: 100vw;
    height: 100vh;
    background-color: rgba(33, 85, 205, 0.9);
    display: flex;
    position: relative;
    overflow-y: hidden;
    /* background-color: beige; */
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}

.ContentSection{
    align-self: center;
    width: 40%;
    height: 500px;
    margin-left: 85px;
    display: flex;
    text-align: start;
    color: white;
    align-items: flex-start;
    flex-direction: column;
    gap: 20px;
    /* justify-content: center; */
    padding-top: 120px;
    padding-left: 10px;
    padding-right: 10px;
    /* border: 10px solid rebeccapurple; */
    /* align-items: center; */
    font-size: 2rem;
}

.Question{
    position: absolute;
    top: 200px;
    width: 570px;
    /* border: 4px solid white; */
}

.Answer{
    position: absolute;
    top: 390px;
    width: 570px;
    /* border: 12px solid black; */
    
}

.ClubCards{
    width: 50%;
    height: 800px;
    display: flex;
    overflow-x: auto;
    gap: 20px;
    /* background-color: antiquewhite; */
    margin-right: 30px;
    white-space: nowrap;
    justify-content: center;
    align-items: center;
    /* font-size: 4rem; */
    /* background-color: burlywood; */
}



.NextCard:hover{
    cursor: pointer;
}

@media (max-width: 898px) {
    .ClubSection{
        flex-direction: column;    
        height: 150vh;
        gap: 2px;
        justify-content: center;

    }

    .ContentSection{
        width: 80%;
        margin-top: 40px;
        /* flex-wrap: wrap; */
        margin-left: 0;
        height: fit-content;
        margin-bottom: 10px;
        padding-top: 5px;
        align-items: center;
        /* border: 2px solid black; */
        /* margin-top: 50px; */
    }

    .Question{
        font-size: 2.3rem;
    }

    p{
        text-align: center;
    }

    .ClubCards{
        width: 70%;
        margin-bottom: 40px;
        display: flex;
        margin-right: 0;
        flex-direction: column;
        /* border: 2px solid black; */
        /* height: 150vh; */
        margin-top: 30px;
        /* margin-top: -150px; */
        
    }

    
}