.ClubCard{
    height: 450px;
    width: 330px;
    /* font-size: 3rem; */
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: whitesmoke;
    border: 3px solid #2155CD;
    border-radius: 20px;
}

.Image{
    margin-top: -30px;
}

.Title{
    font-size: 2.3rem;
    margin-top: 20px;
}


.ViewMoreBtn{
    cursor: pointer;
    margin-top: 50px;
    font-size: 1.3rem;
    color: #2155CD;
    /* border: 2px solid black; */
}
