.Navbar{
    display: flex;
    justify-content: space-between;
    margin-top: 0;
    align-items: center;
    padding: 20px 90px;
    font-size: 1.4rem;
    width: 100vw;
    
    position: fixed;
    top: 0;
    left: 0;
    background-color: white;
    z-index: 2000;
    height: 70px;

}

.Logo{
    font-size: 1.4rem;
}

.NavItems{
    display: flex;
    gap: 30px;
    align-items: center;
}


.NavItem_Member{
    background-color: #0AA1DD;
    padding: 7px;
    border-radius: 10px;
    color: white;
    cursor: pointer;
}

.Hamburger_Icon{
    display: none;

}

@media (max-width: 898px) {
    .Hamburger_Icon{
        display: flex;
    }

    .NavItems{
        display: none;
    }

    .Navbar{
        padding: 20px 30px;
    }


}

.HamburgerMenu{
    display: none;
}