.HeroSection{
    width: 100vw;
    height: 100vh;
    background-color: #2155CD;
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: flex-start;
    
    overflow: auto;
}

.ContentSection{
    align-self: center;
    width: 40%;
    height: 500px;
    /* background-color: wheat; */
    display: flex;
    text-align: start;
    color: white;
    align-items: flex-start;
    flex-direction: column;
    gap: 20px;
    /* justify-content: center; */
    padding-top: 120px;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 100px;
    /* align-items: center; */
    font-size: 2rem;
}



.EventsSection{
    align-self: center;
    width: 50%;
    height: 500px;
    background-color: burlywood;
    display: flex;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
}

@media (max-width: 898px) {
    .HeroSection{
        flex-direction: column-reverse;
        overflow: scroll;
        height: 120vh;
    }

    .ContentSection{
        width: 80%;
        height: 40%;
        margin-bottom: 10px;
        align-items: center;
        padding-top: 30px;
    }
    p{
        text-align: center;
    }

    .EventsSection{
        margin-top: 100px;
        height: 60%;
        width: 80%;
        
    }
}