.Footer{
    height: 300px;
    width: 100vw;  
    font-size: 1.4rem;
    background-color: #E8F9FD;
}

.FooterItems{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    /* border: 2px solid black; */
    height: 70%;
    text-align: center;
}

.Copyright{
    display: flex;
    justify-content: center;
    
    font-size: 2.4rem;
}

@media (max-width: 500px) {
    .FooterItems{
        flex-direction: column;
    }
}